.App {
  height: 100vh;
  width: 100vw;
}

.red-text{
  color: #FF2D2D;
}

.white-text-bold{
  color: #f2f2f2;
  text-decoration: solid;
  font-size: 2em;
  font-weight: bold;
}

.white-text-bold:hover{
  transform: scale(1.1);
}

.light-background{
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  background-color: #f2f2f2;
  height: 100%;
  width: 100%;
}

.red-background{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #FF2D2D;
  height: 100%;
  width: 100%;
}

.text-input{
  background-color: #f2f2f2;
  border-color: #FF2D2D;
  margin: 1rem;
}

.logo{
  width: 80vmin;
  padding: 5%;
  object-fit: contain;
}

.nav-bar{
  display: flex;
    height: 3em;
    width: 100vw;
    align-items: center;
    justify-content: space-between;
    background-color: #FF2D2D;
    margin-bottom: 1rem;
  }
  
  .title{
    margin-left: auto;
    margin-right: auto;
    color: #f2f2f2;
    font-size: 25px;
    font-weight: bold;
}

.button-container{
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 80%;
  flex-wrap: wrap;
}